"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import gsap from "gsap";

import Card from "../../components/ui/Card";
import Select from "../../components/ui/Select";
import Input from "../../components/ui/Input";
import GeneratePanel from "../../components/studio/GeneratePanel";
import PromptComposer from "../../components/studio/PromptComposer";

import styles from "./content.module.css";

const TEMPLATES = [
  {
    id: "ad",
    title: "High-converting ad copy",
    subtitle: "Hook + benefits + CTA",
    prompt:
      "Write a high-converting ad for: {product}. Audience: {audience}. Goal: {goal}. Tone: {tone}. Include 3 hooks, 5 benefits, and 3 CTAs.",
  },
  {
    id: "script",
    title: "Short video script",
    subtitle: "Hook → Story → CTA",
    prompt:
      "Write a short 20s video script about {topic}. Tone: {tone}. Include hook, 3 beats, CTA.",
  },
];

export default function ContentPage() {
  const rootRef = useRef(null);

  const [tplId, setTplId] = useState("ad");
  const [tone, setTone] = useState("Premium");
  const [length, setLength] = useState("Medium");
  const [prompt, setPrompt] = useState(TEMPLATES[0].prompt);

  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const tpl = useMemo(() => TEMPLATES.find((t) => t.id === tplId), [tplId]);

  useEffect(() => {
    setPrompt(tpl.prompt);
  }, [tpl]);

  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.from("[data-anim='card']", {
        y: 12,
        opacity: 0,
        duration: 0.4,
      });
    }, rootRef);
    return () => ctx.revert();
  }, []);

  async function onGenerate() {
    try {
      setLoading(true);
      setError("");
      setResults([]);

      const finalPrompt = `${prompt}
Tone: ${tone}
Length: ${length}`;

      const res = await fetch("/api/content/generate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ prompt: finalPrompt }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.error);

      setResults([
        {
          id: "1",
          content: data.result,
        },
      ]);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className={styles.page} ref={rootRef}>
      <div className={styles.grid}>
        <Card className={styles.left} data-anim="card">
          <div className={styles.tplList}>
            {TEMPLATES.map((t) => (
              <button
                key={t.id}
                className={`${styles.tpl} ${
                  tplId === t.id ? styles.active : ""
                }`}
                onClick={() => setTplId(t.id)}
              >
                <div className={styles.tplTitle}>{t.title}</div>
                <div className={styles.tplSub}>{t.subtitle}</div>
              </button>
            ))}
          </div>

          <div className={styles.controls}>
            <Select value={tone} onChange={(e) => setTone(e.target.value)}>
              <option>Premium</option>
              <option>Bold</option>
              <option>Minimal</option>
            </Select>

            <Select value={length} onChange={(e) => setLength(e.target.value)}>
              <option>Short</option>
              <option>Medium</option>
              <option>Long</option>
            </Select>
          </div>
        </Card>

        <div className={styles.right}>
          <GeneratePanel
            title="Composer"
            subtitle="Generate AI content"
            onGenerate={onGenerate}
            primaryLabel="Generate Content"
          >
            <PromptComposer
              title="Prompt"
              value={prompt}
              onChange={setPrompt}
            />
          </GeneratePanel>

          <div className={styles.results}>
            {loading && <div className={styles.loader}>Generating...</div>}
            {error && <div className={styles.error}>{error}</div>}

            {results.map((r) => (
              <Card key={r.id} className={styles.resultCard}>
                <pre>{r.content}</pre>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
